function CreateCategoryForm() {
  const [categoryName, setCategoryName] = useState("");
  const [images, setImages] = useState<ImageData[]>([]);
  const [isDragging, setIsDragging] = useState(false);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    const files = Array.from(e.dataTransfer.files).filter((file) =>
      file.type.startsWith("image/")
    );
    addImages(files);
  }, []);

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      addImages(files);
    }
  };

  const addImages = (files: File[]) => {
    const newImages: ImageData[] = files.map((file) => ({
      id: Math.random().toString(36).substr(2, 9),
      file,
      preview: URL.createObjectURL(file),
      title: "",
      description: "",
    }));
    setImages((prev) => [...prev, ...newImages]);
  };

  const removeImage = (id: string) => {
    setImages((prev) => {
      const image = prev.find((img) => img.id === id);
      if (image) URL.revokeObjectURL(image.preview);
      return prev.filter((img) => img.id !== id);
    });
  };

  const updateImage = (
    id: string,
    field: "title" | "description",
    value: string
  ) => {
    setImages((prev) =>
      prev.map((img) => (img.id === id ? { ...img, [field]: value } : img))
    );
  };

  const handleSubmit = () => {
    console.log("Creating category:", { categoryName, images });
    // Reset form
    setCategoryName("");
    setImages([]);
  };

  return (
    <div className="space-y-6">
      <div className="space-y-2">
        <Label
          htmlFor="category-name"
          className="text-sm font-medium text-slate-700"
        >
          Category Name
        </Label>
        <Input
          id="category-name"
          value={categoryName}
          onChange={(e) => setCategoryName(e.target.value)}
          placeholder="Enter category name..."
          className="bg-slate-50/50 border-slate-200 focus:border-blue-400 focus:ring-blue-400/20"
        />
      </div>

      <div
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        className={`border-2 border-dashed rounded-xl p-8 text-center transition-all duration-200 ${
          isDragging
            ? "border-blue-400 bg-blue-50/50 scale-[1.01]"
            : "border-slate-200 hover:border-slate-300 bg-slate-50/30"
        }`}
      >
        <div className="w-14 h-14 rounded-2xl bg-gradient-to-br from-blue-100 to-blue-50 flex items-center justify-center mx-auto mb-4">
          <Upload className="h-7 w-7 text-blue-500" />
        </div>
        <p className="text-slate-700 font-medium mb-1">
          Drag and drop images here
        </p>
        <p className="text-slate-400 text-sm mb-4">or</p>
        <label>
          <input
            type="file"
            multiple
            accept="image/*"
            onChange={handleFileSelect}
            className="hidden"
          />
          <Button
            type="button"
            variant="outline"
            className="bg-white border-slate-200 hover:bg-slate-50 hover:border-slate-300"
            asChild
          >
            <span className="cursor-pointer">
              <Plus size={16} className="mr-2" />
              Browse Files
            </span>
          </Button>
        </label>
      </div>

      {images.length > 0 && (
        <div className="space-y-4">
          <h3 className="text-sm font-medium text-slate-600">
            Uploaded Images ({images.length})
          </h3>
          <div className="grid gap-4">
            {images.map((image) => (
              <div
                key={image.id}
                className="bg-slate-50/50 border border-slate-200 rounded-xl p-4 flex flex-col sm:flex-row gap-4"
              >
                <div className="relative flex-shrink-0">
                  <img
                    src={image.preview}
                    alt="Preview"
                    className="w-full sm:w-24 h-24 object-cover rounded-lg"
                  />
                  <button
                    onClick={() => removeImage(image.id)}
                    className="absolute -top-2 -right-2 bg-red-500 text-white rounded-full p-1.5 hover:bg-red-600 transition-colors shadow-lg"
                  >
                    <X size={12} />
                  </button>
                </div>
                <div className="flex-1 space-y-3">
                  <Input
                    value={image.title}
                    onChange={(e) =>
                      updateImage(image.id, "title", e.target.value)
                    }
                    placeholder="Image title..."
                    className="bg-white border-slate-200 text-sm"
                  />
                  <Textarea
                    value={image.description}
                    onChange={(e) =>
                      updateImage(image.id, "description", e.target.value)
                    }
                    placeholder="Image description..."
                    className="bg-white border-slate-200 text-sm resize-none"
                    rows={2}
                  />
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      <Button
        onClick={handleSubmit}
        disabled={!categoryName || images.length === 0}
        className="w-full bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 text-white shadow-lg shadow-blue-500/25 disabled:opacity-50 disabled:shadow-none"
      >
        Create Category
      </Button>
    </div>
  );
}

